html,
*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

* {
  outline: 0;
}

html,
body {
  margin: 0;
  padding: 0;
}

main {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

[hidden] {
  display: none;
}

img {
  max-width: 100%;
  display: inline-block;
  vertical-align: top;
}

a {
  text-decoration: none;
}

:root {
  --c-white: #fff;
  --c-black: #000;
  --c-grey: #e7e7e7;
  --c-grey-dark: #bfbfbf;
  --c-basic: #3558ae;
  --c-primary: #256eff;
  --c-primary-hover: #0051f1;
  --c-secondary: red;
  --c-bg-primary: #f3f4f7;
  --c-bg-secondary: #ffffff;
  --c-bg-tertiary: #fcfdfe;
  --c-border: #f0f1f7;
  --c-shadow: #f3f3f3;
  --c-text-primary: #666666;
  --c-text-secondary: #19235c;
  --c-placeholder: #adb8c6;
  --c-warn: #e67e22;
  --c-error: #e74c3c;
  --c-error-hover: #d62c1a;
  --c-success: #14c76e;
  --c-info: #17a2b8;
  --container: 1210px;
  --b-radius: 8px;
  --b-radius-card: 10px;
  --b-radius-btn: 10px;
  --b-radius-img: 50%;
  --padding: 10px;
  --fs: 16px;
  --ff-bold: bold;
  --ff-base: "Poppins",
      sans-serif;
  --letter-spacing: 0.3px;
  --transition: .3s all ease-in-out;
}

html.dark:root {
  --c-basic: #333;
  --c-bg-primary: #282828;
  --c-bg-secondary: #343434;
  --c-bg-tertiary: #404040;
  --c-border: #404040;
  --c-shadow: #444444;
  --c-text-primary: #ffffff;
  --c-text-secondary: #ffffff;
  --c-placeholder: #7a7a7a;
}

/* USE: @include bp-max(sm)... bp-min... bp-only... */
@font-face {
  font-family: "Poppins";
  src: url("~src/fonts/1.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("~src/fonts/2.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("~src/fonts/3.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Poppins";
  src: url("~src/fonts/4.woff2") format("woff2");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Material Icons";
  font-style: normal;
  font-weight: 400;
  src: url(~src/fonts/5.woff2) format("woff2");
}
.material-icons, i {
  font-family: "Material Icons";
  font-weight: normal;
  font-style: normal;
  font-size: 26px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
}

html {
  font-size: var(--fs);
  overflow: hidden;
  height: 100%;
}

body {
  font-family: var(--ff-base);
  color: var(--c-text-primary);
  line-height: 1.4;
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
}

/* === PAGE LAYOUT === */
.container {
  margin-left: auto;
  margin-right: auto;
  padding: var(--padding);
  max-width: var(--container);
  width: 100%;
}

*::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px var(--c-shadow);
  background-color: var(--c-white);
  display: none;
}

*::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: var(--c-white);
}

* ::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px var(--c-basic);
  background-color: var(--c-basic);
}

.w-btn {
  font-size: var(--fs);
  font-weight: 500;
  font-family: var(--ff-base);
  background-color: var(--c-primary);
  border-radius: var(--b-radius);
  transition: var(--transition);
  color: var(--c-white);
  position: relative;
  padding: 11px 25px;
  line-height: 19px;
  text-align: center;
  white-space: nowrap;
  user-select: none;
  border: none;
  cursor: pointer;
  margin: 5px;
}
.w-btn._primary {
  background-color: var(--c-primary);
}
.w-btn._primary:hover {
  background-color: var(--c-primary-hover);
}
.w-btn._second {
  border: 1px solid #256EFF;
  background-color: var(--c-white);
  color: #256EFF;
}
.w-btn._danger {
  background-color: var(--c-error);
  color: white;
}
.w-btn._danger:hover {
  background: var(--c-error-hover);
}
.w-btn._link {
  background: transparent;
  color: var(--c-text-primary);
  font-size: 14px;
  font-weight: 400;
}
.w-btn._link:hover {
  color: var(--c-text-secondary);
}
.w-btn:disabled {
  background-color: var(--c-grey-dark);
  cursor: default;
}
.w-btn:disabled:hover {
  background-color: var(--c-grey-dark);
}

.w-switch {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.w-switch__toggle {
  position: relative;
  width: 45px;
  height: 25px;
}
.w-switch__input {
  display: none;
}
.w-switch__input:checked + .w-switch__slider {
  background-color: var(--c-primary);
  border: 1px solid var(--c-primary);
}
.w-switch__input:focus + .w-switch__slider {
  box-shadow: 0 0 1px var(--c-shadow);
}
.w-switch__input:checked + .w-switch__slider:before {
  transform: translate(15px, -50%);
  background-color: var(--c-white);
}
.w-switch__slider {
  position: absolute;
  inset: 0;
  background-color: var(--c-grey);
  border: 1px solid var(--c-border);
  outline: none;
  transition: var(--transition);
}
.w-switch__slider:before {
  position: absolute;
  content: "";
  height: 16px;
  width: 16px;
  left: 6px;
  top: 50%;
  transform: translateY(-50%);
  background-color: var(--c-white);
  transition: var(--transition);
}
.w-switch__slider._round {
  border-radius: 34px;
}
.w-switch__slider._round:before {
  border-radius: 50%;
}
.w-switch__text {
  margin-left: 10px;
  font-size: calc(var(--fs) - 2px);
  line-height: calc(var(--fs) - 2px);
  letter-spacing: var(--letter-spacing);
  color: var(--c-text);
}

.w-forms {
  position: relative;
  margin: 10px 0;
}
.w-forms__title {
  display: inline-block;
  color: var(--c-text-primary);
  font-size: calc(var(--fs) - 4px);
  line-height: calc(var(--fs) + 2px);
  letter-spacing: var(--letter-spacing);
  margin-bottom: 6px;
  font-weight: 500;
}
.w-forms__input, .w-forms__textarea, .w-forms__select {
  height: 42px;
  background-color: var(--c-bg-tertiary);
  border: 1px solid var(--c-border);
  border-radius: var(--b-radius);
  color: var(--c-text-secondary);
  font-size: calc(var(--fs) - 2px);
  line-height: calc(var(--fs) + 4px);
  transition: var(--transition);
  width: 100%;
  display: block;
  letter-spacing: var(--letter-spacing);
  padding: 10px;
}
.w-forms__input::placeholder, .w-forms__textarea::placeholder, .w-forms__select::placeholder {
  color: var(--c-placeholder);
  transition: var(--transition);
}
.w-forms__input:focus, .w-forms__textarea:focus, .w-forms__select:focus {
  transition: var(--transition);
  border-color: var(--c-primary);
}
.w-forms__input:disabled, .w-forms__textarea:disabled, .w-forms__select:disabled {
  background-color: var(--c-grey);
  border-color: var(--c-grey);
  color: var(--c-grey);
  cursor: default;
  opacity: 0.6;
}
.w-forms__input[type=time] {
  display: block;
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
}
.w-forms__select {
  appearance: none;
  padding-right: 30px;
}
.w-forms__chevron {
  position: absolute;
  top: 30px;
  right: 12px;
}
.w-forms__chevron::before {
  color: var(--c-primary);
}
.w-forms__textarea {
  min-height: 80px;
  resize: vertical;
  max-height: 250px;
}

.w-card {
  background: var(--c-bg-secondary);
  border-radius: var(--b-radius-card);
  box-shadow: 0px 0px 6px var(--c-shadow);
}
.w-card._pd {
  padding: 30px;
}
.w-card__header {
  min-height: 60px;
}

.w-checkbox__body {
  display: inline-flex;
  user-select: none;
  color: var(--c-text-primary);
  cursor: pointer;
  padding: 6px 8px;
  border-radius: 6px;
  overflow: hidden;
  transition: var(--transition);
}
.w-checkbox__body:not(:last-child) {
  margin-right: 6px;
}
.w-checkbox__body:hover {
  background: rgba(var(--c-primary), 0.09);
}
.w-checkbox__body:hover .w-checkbox__svg {
  border-color: var(--c-primary);
}
@media (max-width: 991.9px) {
  .w-checkbox__body {
    width: 100%;
    margin-bottom: 4px;
    display: inline-block;
  }
}
.w-checkbox__input {
  display: none;
}
.w-checkbox__input:checked + .w-checkbox__body .w-checkbox__svg {
  background: var(--c-primary);
  border-color: var(--c-primary);
  animation: wave 0.4s ease;
}
.w-checkbox__input:checked + .w-checkbox__body .w-checkbox__svg svg {
  stroke-dashoffset: 0;
}
.w-checkbox__svg {
  position: relative;
  width: 18px;
  height: 18px;
  display: inline-block;
  border-radius: 4px;
  transform: scale(1);
  border: 1px solid var(--c-border);
  transition: var(--transition);
  box-shadow: 0 1px 1px var(--c-shadow);
}
.w-checkbox__svg svg {
  position: absolute;
  top: 3px;
  left: 2px;
  fill: none;
  stroke: #fff;
  stroke-width: 2;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 16px;
  stroke-dashoffset: 16px;
  transition: all 0.3s ease;
  transition-delay: 0.1s;
  transform: translate3d(0, 0, 0);
}
.w-checkbox__text {
  padding-left: 8px;
  line-height: 18px;
}
.w-checkbox__svg-icon {
  position: absolute;
  width: 0;
  height: 0;
  pointer-events: none;
  user-select: none;
}

@keyframes wave {
  50% {
    transform: scale(0.9);
  }
}
.w-radio {
  position: relative;
  display: flex;
  align-items: center;
  cursor: pointer;
  line-height: 20px;
  font-size: 16px;
  margin: 5px 0;
}
.w-radio:hover .w-radio__label:after {
  transform: scale(3.6);
}
.w-radio__label {
  position: relative;
  display: block;
  float: left;
  margin-right: 10px;
  width: 20px;
  height: 20px;
  border: 2px solid var(--c-border);
  border-radius: 100%;
  min-width: 20px;
}
.w-radio__label:after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background: var(--c-primary);
  transform: scale(0);
  transition: var(--transition);
  opacity: 0.08;
  pointer-events: none;
}
.w-radio__input {
  display: none;
}
.w-radio__input:checked + .w-radio__label {
  border-color: var(--c-primary);
}
.w-radio__input:checked + .w-radio__label:after {
  transform: scale(1);
  transition: all 0.2s cubic-bezier(0.35, 0.9, 0.4, 0.9);
  opacity: 1;
}

.w-table-wrap {
  background: var(--c-bg-secondary);
  box-shadow: var(--c-shadow);
  border-radius: var(--b-radius);
  padding: 20px;
  width: 100%;
  transition: var(--transition);
}
.w-table-wrap .w-table {
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
}
.w-table-wrap .w-table__th {
  padding: 15px 10px;
  text-align: center;
  border-right: 2px solid var(--c-border);
  border-bottom: 0.5px solid var(--c-border);
  font-size: var(--fs);
  line-height: calc(var(--fs) + 8px);
  letter-spacing: var(--letter-spacing);
  color: var(--c-text-primary);
  text-overflow: ellipsis;
  overflow: hidden;
  text-transform: capitalize;
  font-weight: 500;
}
.w-table-wrap .w-table__th:last-child {
  border-right: none;
}
@media (max-width: 991.9px) {
  .w-table-wrap .w-table__th {
    font-size: calc(var(--fs) - 4px);
  }
}
.w-table-wrap .w-table__td {
  cursor: pointer;
  padding: 15px 10px;
  text-align: center;
  vertical-align: middle;
  border-right: 2px solid var(--c-border);
  border-bottom: 2px dashed var(--c-border);
  font-size: calc(var(--fs) - 2px);
  line-height: calc(var(--fs) + 8px);
  letter-spacing: var(--letter-spacing);
  color: var(--c-text-primary);
  word-break: break-word;
}
@media (max-width: 991.9px) {
  .w-table-wrap .w-table__td {
    font-size: calc(var(--fs) - 4px);
  }
}
.w-table-wrap .w-table-body__tr:last-child .w-table__td {
  border-bottom: none;
}
.w-table-wrap .w-table-body__tr .w-table__td:last-child,
.w-table-wrap .w-table-body__tr .w-table__th:last-child {
  border-right: none;
  border-right: none;
}
@media (max-width: 991.9px) {
  .w-table-wrap .w-table-body__tr:nth-child(even) {
    background: rgba(0, 0, 0, 0.04);
  }
}

@media (max-width: 1979.9px) {
  .xl-hide {
    display: none;
  }
  .xl-br-none {
    border-right: none !important;
  }
}
@media (max-width: 767.9px) {
  .sm-hide {
    display: none;
  }
  .sm-br-none {
    border-right: none !important;
  }
}
#html .modal {
  padding: 20px;
}
#html .modal-content {
  background: var(--c-bg-secondary);
  border-radius: var(--b-radius);
  border: 1px solid var(--c-border);
}
#html .modal .close:hover {
  color: var(--c-text-secondary);
}

#html body._modal-small .modal-content {
  max-width: 500px;
  width: 100%;
  margin: 20px auto;
}